import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import Navigation from './components/Navigation'
import vueSmoothScroll from 'vue-smooth-scroll'

import './assets/style.sass'

Vue.use(vueSmoothScroll)
Vue.use(VueRouter)

const router = new VueRouter({
	mode: 'history',	
})

new Vue({
	el: '#app',
	router,
  data: function () {
		return {
			menu_open: false,
			scrollPosition: null,
			scrollPosition_1: null,
			scrollPosition_2: null,
			scrollPosition_3: null,
			scrollPosition_4: null,
			scrollPosition_5: null,
			windowWidth: 0,
		}
	},
	// methods: {
  //   updateScroll() {
	// 		// this.scrollPosition = window.scrollY
	// 		let obj_1 = document.getElementById('about')
	// 		this.scrollPosition = obj_1.offsetTop + '' + 100
  //   }
  // },
	mounted() {
		this.$nextTick(function() {
			window.addEventListener('resize', this.getWindowWidth);	
			window.addEventListener('scroll', this.updateScroll);
			this.getWindowWidth()
				this.updateScroll()
		})
	},
	methods: {
		getWindowWidth(event) {
			this.windowWidth = document.documentElement.clientWidth;
		},
		updateScroll: function() {
			this.scrollPosition =  window.scrollY
			this.scrollPosition_1 =  document.getElementById('header').offsetTop - scrollY
			this.scrollPosition_2 =  document.getElementById('about').offsetTop - scrollY
			this.scrollPosition_3 =  document.getElementById('projects').offsetTop - scrollY
			this.scrollPosition_4 =  document.getElementById('team').offsetTop - scrollY
			this.scrollPosition_5 =  document.getElementById('contact').offsetTop - scrollY
		}
	},
	beforeDestroy() {
		window.removeEventListener('resize', this.getWindowWidth);
		window.removeEventListener('scroll', this.updateScroll);
	}
})


